import React from "react";

const ProfileImage = () => {
  const  data = null;
  return (
    <img
      src={
        `${data?.data?.root_path}/${data?.data?.data[0].filename}`
          ? `${data?.data?.root_path}/${data?.data?.data[0].filename}`
          : "images/profile-img.png"
      }
      alt="profile image"
    />
  );
};

export default ProfileImage;
