import React from "react";
import { useDispatch } from "react-redux";
import ProfileImage from "./ProfileImage";
const Header = () => {
  const dispatch = useDispatch();
  const data = null;
 
  return (
    <header className="mainHead">
      <div className="container-fluid">
        <div className="d-flex align-items-center flex-wrap justify-content-between">
          <a href="#">
            <img src="images/logo.png" alt="" width={110} />
          </a>
          <div className="topRightInner">
            <div className="searchBox">
              <input
                type="text"
                className="form-control"
                placeholder="Search here.."
              />
              <button type="button" className="searchButton">
                {" "}
                <img src="images/search-icon.svg" alt="" width={30} />
              </button>
            </div>
            <div className="notficationButton">
              <a href="#">
                <img src="images/notification-icon.svg" alt="" width={35} />
              </a>
            </div>
            <div className="dropdown priofileDropdown">
              <button
                className="dropdown-toggle"
                type="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <ProfileImage />{" "}
                {`${data?.data[0].first_name} ${data?.data[0].last_name}`}
              </button>
              <ul className="dropdown-menu">
                <li>
                  <a className="dropdown-item" href="#;">
                    My Profile
                  </a>
                </li>
                <li>
                  <a className="dropdown-item">Change Password</a>
                </li>
                <li>
                  <a
                    className="dropdown-item"
                    onClick={() => alert('logout')}
                  >
                    Log Out
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
