import React from "react";

const AlertMessage = () => {
  return (
    <div className="alertsSec cardBox mb-3 p-3">
      <h3 className="fs-6 fw-regular text-orange d-flex align-items-center gap-1 mb-0">
        <img src="images/alert.svg" alt="" width={15} />
        Alerts
      </h3>
      <div className="alertList dashboardAlrt">
        <marquee>
          <a className="pe-2">
            Hey Nikita Prajapat, your attendance correction of{" "}
            <span className="text-danger"> 12 Feb 2024</span> will get locked by
            today, please check your calendar and do corrections if required
            before 12 AM
          </a>
        </marquee>
      </div>
    </div>
  );
};

export default AlertMessage;
